import BaseField from "~/components/fields/BaseField"
import Utils from '~/base/Utils';
import ss from 'simple-ajax-uploader';
import axios from 'axios';

class ImageField extends BaseField {

    constructor(opts) {
        super(opts);

        this.onDeleteLocalized = this.onDeleteLocalized.bind(this);
        this.deleteLocalized = this.deleteLocalized.bind(this);
    }

    getDefaultOptions() {
        return {
            height: "200px",
            endpoints: {
                getPreview: 'path-to-server-get-preview-page',
                upload: 'path-to-server-upload-page',
                delete: 'path-to-server-delete-page'
            },
            labels: {
                chooseFile: "Choose file",
                chooseAnotherFile: "Choose another file",
                uploading: "Uploading ...",
                unableToUpload: "Unable to upload file",
                genericError: "An error occurred.",
                successfullyUploaded: " successfully uploaded.",
                delete: "Delete",
                deleteTitle: "Confirm delete",
                deleteMessage: "Do you want to delete the image ?",
                confirm: "Confirm",
                cancel: "Cancel",
                invalidFileType: "Invalid file type",
                fileTooBig: "File too big"
            },
            allowedFileTypes: "*",
            maxFileSize: 1024,
            showMessage: true
        }
    }

    createLocalizedField(lang) {
        const uid = this.id() + "-" + lang;
        const title = Utils.NoLang() === lang ? '' : ' title="' + this.settings.languages[lang] + '" ';
        var elHtml = '\
            <div id="upload-container-' + uid + '" class="composer-field image-upload-container" ' + title + ' data-lang="' + lang + '">\
                <button class="upload-btn" style="height: ' + this.settings.height + ';">' + this.settings.labels.chooseFile + '</button>\
                <div class="progress-bar-container">\
                    <div class="progress-bar" role="progressbar" aria-valuenow="0" aria-valuemin="0" aria-valuemax="100"></div>\
                </div>\
                <div class="tasks">\
                    <span class="task task-delete">' + this.settings.labels.delete + '</span>\
                </div>\
                <div class="msgbox"></div>\
                <input type="hidden" class="upload-value" id="upload-value-' + uid + '" name="upload-value-' + uid + '" value="' + uid + '"/>\
            </div>';

        const parsedEl = new DOMParser().parseFromString(elHtml, "text/html");
        return super.find(parsedEl, "#upload-container-" + uid);
    }

    getComponentContainer(uid) {
        return super.find(document, "#upload-container-" + uid);
    }

    getSubComponent(container, selector) {
        if (typeof container === "string") {
            if (container.match(/^[0-9a-z]{36}/i)) {
                return super.find(super.find(document, "#upload-container-" + container), selector);
            } else {
                return super.find(super.find(document, "#" + container), selector);
            }
        }
        else {
            return super.find(container, selector);
        }
    }

    init() {

        for (var [lang, el] of super.getComponents()) {

            let uid = this.id() + "-" + lang;

            let container = this.getComponentContainer(uid);

            let uploadBtn = this.getSubComponent(container, '.upload-btn');
            let progressBar = this.getSubComponent(container, '.progress-bar');
            let progressContainer = this.getSubComponent(container, '.progress-bar-container');
            let msgBox = this.getSubComponent(container, '.msgbox');
            let deleteBtn = this.getSubComponent(container, '.task-delete');

            deleteBtn.addEventListener("click", () => {
                this.onDeleteLocalized(uid);
            });

            msgBox.style.display = this.settings.showMessage ? "block" : "none";

            let uploader = new ss.SimpleUpload({
                button: uploadBtn,
                url: this.settings.endpoints.upload,
                name: 'uploadfile',
                multipart: true,
                hoverClass: 'hover',
                focusClass: 'focus',
                responseType: 'json',
                dropzone: uploadBtn,
                data: {
                    parameters: this.buildData(uid)
                },
                startXHR: () => {
                    progressContainer.style.transitionDuration = "0s";
                    progressContainer.style.opacity = '1';
                    progressBar.style.width = "0px";
                    uploader.setProgressBar(progressBar);
                    uploader.setData({
                        parameters: this.buildData(uid)
                    });
                },
                onProgress: (pct) => {
                    progressBar.style.width = pct + "%";
                    progressBar.innerText = pct + "%";
                },
                onExtError: function (filename, extension) {
                    Utils.AlertError(this.settings.labels.invalidFileType);
                },
                onSizeError: function (filename, fileSize) {
                    Utils.AlertError(this.settings.labels.fileTooBig);
                },
                onSubmit: () => {
                    msgBox.innerHTML = '';
                    uploadBtn.innerHTML = this.settings.labels.uploading;
                },
                onComplete: (filename, response) => {
                    if (response && response.status === true) {
                        if (response.preview) {                            
                            const preview = response.preview + "&" + this.getSizeForQueryString(uploadBtn);
                            uploadBtn.style.backgroundImage = "url('" + preview + "')";
                        }

                        uploadBtn.innerHTML = this.settings.labels.chooseAnotherFile;
                        deleteBtn.style.display = "inline";
                        msgBox.innerHTML = '<strong>' + Utils.EscapeTags(response.fileName) + '</strong>';
                        progressContainer.style.transitionDuration = "2s";
                        progressContainer.style.opacity = '0';
                    } else {
                        msgBox.innerHTML = this.settings.labels.unableToUpload;
                    }

                    super.signalChange();
                },
                onError: () => {
                    progressContainer.style.transitionDuration = "2s";
                    progressContainer.style.opacity = '0';
                    msgBox.innerHTML = this.settings.labels.unableToUpload;
                    super.signalChange();
                }
            });
        }
    }

    buildData(uid) {
        const valueEl = this.getSubComponent(uid, '.upload-value');
        
        return Utils.ObjToBase64({
            "UniqueID": valueEl.value,
            "AllowedFileTypes": this.settings.allowedFileTypes,
            "MaxFileSize": this.settings.maxFileSize
        });
    }

    onDeleteLocalized(uid) {
        Utils.AlertWarning(this.settings.labels.deleteTitle, this.settings.labels.deleteMessage, true)
            .then(() => {
                this.deleteLocalized(uid).catch(() => {
                    Utils.AlertError();
                });
            });
    }

    deleteLocalized(uid) {

        return new Promise((resolve, reject) => {

            axios.get(this.settings.endpoints.delete + "&parameters=" + this.buildData(uid))
                .then(() => {

                    const uploadBtn = this.getSubComponent(uid, '.upload-btn');
                    const progressBar = this.getSubComponent(uid, '.progress-bar');
                    const progressContainer = this.getSubComponent(uid, '.progress-bar-container');
                    const msgBox = this.getSubComponent(uid, '.msgbox');
                    const deleteBtn = this.getSubComponent(uid, '.task-delete');

                    uploadBtn.style.removeProperty("background-image");
                    uploadBtn.innerHTML = this.settings.labels.chooseFile;

                    progressContainer.style.transitionDuration = "0s";
                    progressContainer.style.opacity = '0';

                    progressBar.style.width = "0px";

                    msgBox.innerText = "";

                    deleteBtn.style.display = "none";
                    super.signalChange();
                    resolve();
                })
                .catch(() => {
                    Utils.AlertError();
                    reject();
                });
        });
    }

    delete() {

        const deletePromises = [];

        for (var [lang, el] of super.getComponents()) {
            deletePromises.push(this.deleteLocalized(el.getAttribute("id")));
        }

        return Promise.all(deletePromises);
    }

    getValueFromLocalizedField(el, lang) {
        return this.getSubComponent(el, '.upload-value').value;
    }

    setValueToLocalizedField(el, value, lang) {
        const valueEl = this.getSubComponent(el, '.upload-value');
        valueEl.value = value;
        axios.get(this.settings.endpoints.getPreview + "&parameters=" + this.buildData(el))
            .then((response) => {

                if (response.data && response.data.preview) {
                    const uploadBtn = this.getSubComponent(el, '.upload-btn');
                    const deleteBtn = this.getSubComponent(el, '.task-delete');

                    const preview = response.data.preview + "&" + this.getSizeForQueryString(uploadBtn, 200);

                    uploadBtn.style.backgroundImage = "url('" + preview + "')";
                    uploadBtn.innerHTML = this.settings.labels.chooseAnotherFile;

                    deleteBtn.style.display = "inline";
                }
            })
            .catch(() => {
                Utils.AlertError();
            });
    }
}

export default ImageField


