import BaseField from "~/components/fields/BaseField"

class SelectField extends BaseField {

    constructor(opts) {
        super(opts);
    }

    createLocalizedField (lang)    {

        const uid = this.id() + "-" + lang;

        var elHtml = '<select id="select-' + uid + '" class="composer-field select" data-lang="' + lang + '">';
        const opts = this.settings.values;
        if (opts) {
            var first = true;
            for (var optValue in opts) {
                var selected = (first ? ' selected="selected" ' : '');
                elHtml += '<option ' + selected + ' value="' + optValue + '">' + opts[optValue] + '</option>';
                first = false;
            }
        }

        elHtml += '</select>';
        const parsedEl = new DOMParser().parseFromString(elHtml, "text/html");
        return super.find(parsedEl, "#select-" + uid);
    }

    init () {
        
    }
}

export default SelectField



