import Component from "~/base/Component"
import Utils from "~/base/Utils"
import classie from "classie"
import BaseField from "~/components/fields/BaseField"
import base64 from 'base-64';
import _ from "lodash";

class BaseRender extends Component {

    constructor(opts) {
        super(null, opts);
        this.fields = Array();
        this.onChildChanged = this.onChildChanged.bind(this);
    }

    onChildChanged() {

    };

    init(el) {

        this.createContainer();

        this.createFields();

        var blockData = {};

        if (Utils.Data(el, "pagecomposeredit") == 1) {
            blockData = JSON.parse(base64.decode(el.innerText.trim()));
        }

        el.replaceWith(this.containerEl);

        for (var field in this.fields) {
            var currentField = this.fields[field];
            if (currentField) {
                if (typeof (currentField.init) === "function") {
                    currentField.init();
                }

                const fieldName = currentField.getFieldName();
                if (blockData.hasOwnProperty(fieldName) && typeof (currentField.setValue) === "function") {
                    currentField.setValue(blockData[fieldName]);
                }
            }
        }

        Utils.Data(this.containerEl, "pagecomposerblockrendered", 1);
    }

    getDefaultOptions() {
        return {
            changeCallback: this.onChildChanged
        }
    }

    getSettingsForChild(isLocalized, opts = {}) {

        let result = _.merge(opts, {
            changeCallback: this.settings.changeCallback,
            endpoints: this.settings.endpoints
        });

        if (isLocalized) {
            result.languages = this.settings.languages;
            result.currentLang = this.settings.currentLang;
        }

        return result;
    }

    createFields() {

    }

    wrapField(el, label) {
        const wrapperEl = document.createElement("div");
        classie.add(wrapperEl, "field-wrapper");

        const labelContainerEl = document.createElement("div");
        classie.add(labelContainerEl, "field-label-container");

        const labelEl = document.createElement("span");
        labelEl.innerText = label;

        labelContainerEl.appendChild(labelEl);

        wrapperEl.appendChild(labelContainerEl);

        if (Array.isArray(el)) {
            el.forEach(function(child) {
                wrapperEl.appendChild(child);
            }, this);
        } else {
            wrapperEl.appendChild(el);
        }

        return wrapperEl;
    }

    createContainer() {
        const containerEl = document.createElement("div");

        classie.add(containerEl, "render-container");
        classie.add(containerEl, "block-item");
        classie.add(containerEl, _.snakeCase(this.constructor.name).replace("_", "-"));

        Utils.Data(containerEl, "pagecomposeruid", this.id());

        const taskHtml = '\
            <div class="block-task u-text--right">\
                <span class="block-task-icon task-move"></span>\
                <span class="block-task-icon task-delete"></span>\
            </div>';

        containerEl.appendChild(super.find(new DOMParser().parseFromString(taskHtml, "text/html"), ".block-task"));

        const separatorEl = document.createElement("hr");

        containerEl.appendChild(separatorEl);

        this.containerEl = containerEl;
    }

    addField(id, field) {
        if (field && field instanceof BaseField) {
            field.setFieldName(id);
            this.fields[id] = field;
            return true;
        }
        return false;
    }

    getField(id) {
        if (this.fields && this.fields[id]) {
            return this.fields[id];
        }
        return null;
    }

    appendField(id, field, label) {
        if (this.addField(id, field)) {
            this.addToLayout(this.wrapField(field.build(), label));
        }
    }

    addToLayout(el) {
        const separatorEl = this.getSeparator();
        this.containerEl.insertBefore(el, separatorEl);
    }

    getSeparator() {
        return this.containerEl.querySelectorAll("hr")[0];
    }

    getValue() {
        var obj = {};
        for (var key in this.fields) {
            let field = this.fields[key];
            if (field.getValue && typeof field.getValue === "function") {
                obj[key] = field.getValue();
            }
        }
        return obj;
    }

    setCurrentLanguage(lang) {
        for (var key in this.fields) {
            let field = this.fields[key];
            if (field.setCurrentLanguage && typeof field.setCurrentLanguage === "function") {
                field.setCurrentLanguage(lang);
            }
        }
    }
}

export default BaseRender


