import guid from 'guid'
import base64 from 'base-64';
import { default as swal } from 'sweetalert2';
import merge from 'lodash/merge';

class Utils {

    static GetDefaultOptions() {
        return {
            alerts: {
                showCancelButton: false,
                confirmLabel: "Ok",
                cancelLabel: "Cancel",
                genericError: "An error occurred."
            }
        };
    }

    static GetSettings() {
        if (!Utils.Settings)
            Utils.Settings = merge({}, Utils.GetDefaultOptions());
        return Utils.Settings;
    }

    static SetSettings(options) {
        return Utils.Settings = merge({}, Utils.GetDefaultOptions(), options);
    }

    //http://stackoverflow.com/questions/10716986/swap-2-html-elements-and-preserve-event-listeners-on-them
    static SwapElements(obj1, obj2) {
        // save the location of obj2
        var parent2 = obj2.parentNode;
        var next2 = obj2.nextSibling;

        // special case for obj1 is the next sibling of obj2
        if (next2 === obj1) {
            // just put obj1 before obj2
            parent2.insertBefore(obj1, obj2);
        } else {
            // insert obj2 right before obj1
            obj1.parentNode.insertBefore(obj2, obj1);

            // now insert obj1 where obj2 was
            if (next2) {
                // if there was an element after obj2, then insert obj1 right before that
                parent2.insertBefore(obj1, next2);
            } else {
                // otherwise, just append as last child
                parent2.appendChild(obj1);
            }
        }
    }

    static ReplaceClass(el, from, to) {
        if (el.classList.contains(from)) {
            el.classList.remove(from);
            el.classList.add(to);
        }
    }

    static InvertUnsemanticGridPrefixSuffix(obj) {
        const tmpClass = "tmp_" + this.Uid() + "_class";
        for (var i = 5; i < 100; i += 5) {
            this.ReplaceClass(obj, "prefix-" + i, tmpClass + "-" + i);
            this.ReplaceClass(obj, "suffix-" + i, "prefix-" + i);
            this.ReplaceClass(obj, tmpClass + "-" + i, "suffix-" + i);
        }
    }

    static FadeSwapElements(obj1, obj2) {
        const _this = this;

        obj1.classList.add("fade-transition");
        obj2.classList.add("fade-transition");

        obj1.style.opacity = 0;
        obj2.style.opacity = 0;

        setTimeout(function() {

            _this.InvertUnsemanticGridPrefixSuffix(obj1);
            _this.InvertUnsemanticGridPrefixSuffix(obj2);

            _this.SwapElements(obj1, obj2);

            obj1.style.opacity = 1;
            obj2.style.opacity = 1;

            setTimeout(function() {
                obj1.classList.remove("fade-transition");
                obj2.classList.remove("fade-transition");
            }, 400);
        }, 400);
    }

    static Data(el, key, value) {
        if (value !== undefined) {
            el.dataset[key] = value;
        } else {
            return el.dataset[key];
        }
    }

    static Uid() {
        return guid.create().toString();
    }

    static EscapeTags(str) {
        return String(str)
            .replace(/&/g, '&amp;')
            .replace(/"/g, '&quot;')
            .replace(/'/g, '&#39;')
            .replace(/</g, '&lt;')
            .replace(/>/g, '&gt;');
    }

    static ObjToBase64(obj) {
        return base64.encode(JSON.stringify(obj));
    }

    static Base64ToObj(str, defaultValue = {}) {
        try {
            return JSON.parse(base64.decode(str));
        }
        catch (exc) {
            return defaultValue;
        }
    }

    static FindAncestorByClass(el, cls) {
        while ((el = el.parentElement) && !el.classList.contains(cls));
        return el;
    }

    static NoLang() {
        return "no-lang";
    }

    static AlertError(title, message, showCancel, confirmLabel, cancelLabel) {
        return this.Alert("error", title || Utils.GetSettings().alerts.genericError, message, showCancel, confirmLabel, cancelLabel);
    }

    static AlertWarning(title, message, showCancel, confirmLabel, cancelLabel) {
        return this.Alert("warning", title, message, showCancel, confirmLabel, cancelLabel);
    }

    static AlertInfo(title, message, showCancel, confirmLabel, cancelLabel) {
        return this.Alert("info", title, message, showCancel, confirmLabel, cancelLabel);
    }

    static Alert(type, title, message, showCancel, confirmLabel, cancelLabel) {
        return swal({
            title: title,
            text: message || "",
            type: type,
            showCancelButton: showCancel || Utils.GetSettings().alerts.showCancelButton,
            confirmButtonText: confirmLabel || Utils.GetSettings().alerts.confirmLabel,
            cancelButtonText: cancelLabel || Utils.GetSettings().alerts.cancelLabel,
        });
    }
}

export default Utils


