import BaseRender from "~/components/render/BaseRender";
import Utils from "~/base/Utils";
import * as Fields from "~/components/fields";
import classie from "classie";
import _ from 'lodash';

// ImageBlockItem
class ImageRender extends BaseRender {

    constructor(opts) {
        super(opts);
    }

    createFields() {

        /* Image field */
        const imageField = new Fields.ImageField(super.getSettingsForChild(false));

        /* Image position field */
        const imagePositionField = new Fields.SelectField(super.getSettingsForChild(false, {
            "values": {
                "center": "Centrale"
            }
        }));

        /* Type field */
        const imageTypeField = new Fields.SelectField(super.getSettingsForChild(false, {
            "values": {
                "full": "Full",
                "small": "Small"
            }
        }));

        /* Caption field */
        const captionField = new Fields.RichTextField(super.getSettingsForChild(true, {
            "showCode": true
        }));

        /* Caption position field */
        const captionPositionField = new Fields.SelectField(super.getSettingsForChild(false, {
            "values": {
                "top": "Sopra",
                "bottom": "Sotto"
            }
        }));

        /* Abstract field */
        const abstractField = new Fields.RichTextField(super.getSettingsForChild(true, {
            "showCode": true,
            "toolbar": 'undo redo | bold | alignleft aligncenter alignright | removeformat',
        }));

        /* Abstract style field */
        const abstractStyleField = new Fields.SelectField(super.getSettingsForChild(false, {
            "values": {
                "normal": "Normale",
                "gradient": "Gradiente arancione"
            }
        }));

        /* Text field */
        const textField = new Fields.RichTextField(super.getSettingsForChild(true, {
            "showCode": true
        }));                

        super.appendField("image", imageField, "Immagine");
        super.appendField("imagePosition", imagePositionField, "Posizione immagine");
        super.appendField("imageType", imageTypeField, "Tipo immagine");
        super.appendField("imageCaption", captionField, "Didascalia (opzionale)");
        super.appendField("imageCaptionPosition", captionPositionField, "Posizione didascalia");
        super.appendField("abstract", abstractField, "Sottotitolo");
        super.appendField("abstractStyle", abstractStyleField, "Stile sottotitolo");
        super.appendField("text", textField, "Testo (opzionale)");
    }

    init(el) {
        super.init(el);
    }

    delete() {
        return super.getField("image").delete();
    }
}

export default ImageRender


