import BaseRender from "~/components/render/BaseRender";
import Utils from "~/base/Utils";
import * as Fields from "~/components/fields";
import classie from "classie";
import _ from 'lodash';

// ImageTextBlockItem
class ImageTextRender extends BaseRender {

    constructor(opts) {
        super(opts);
    }

    createFields() {

        const columnContainerEl = document.createElement("div");
        classie.add(columnContainerEl, "grid-container");
        classie.add(columnContainerEl, "grid-parent");
        classie.add(columnContainerEl, "columns-container");

        const leftContainerEl = document.createElement("div");
        classie.add(leftContainerEl, "grid-45");
        classie.add(leftContainerEl, "suffix-5");
        classie.add(leftContainerEl, "left-column");

        const rightContainerEl = document.createElement("div");
        classie.add(rightContainerEl, "grid-45");
        classie.add(rightContainerEl, "prefix-5");
        classie.add(rightContainerEl, "right-column");

        /* Image field */
        const imageField = new Fields.ImageField(super.getSettingsForChild(false));

        /* Caption field */
        const captionField = new Fields.RichTextField(super.getSettingsForChild(true, {
            "showCode": true
        }));

        /* Subtitle field */
        const subtitleField = new Fields.RichTextField(super.getSettingsForChild(true, {
            "showCode": true,
            "toolbar": 'undo redo | bold | alignleft aligncenter alignright | removeformat',
        }));

        /* Subtitle style field */
        const subtitleStyleField = new Fields.SelectField(super.getSettingsForChild(false, {
            "values": {
                "normal": "Normale",
                "gradient": "Gradiente arancione"
            }
        }));

        /* Text field */
        const textField = new Fields.RichTextField(super.getSettingsForChild(true, {
            "showCode": true
        }));

        /* Image position field */
        const imagePositionField = new Fields.SelectField(super.getSettingsForChild(false, {
            "values": {
                "dx": "Destra",
                "sx": "Sinistra"
            }
        }));

        /* Caption position field */
        const captionPositionField = new Fields.SelectField(super.getSettingsForChild(false, {
            "values": {
                "top": "Sopra",
                "bottom": "Sotto"
            }
        }));

        super.appendField("imagePosition", imagePositionField, "Posizione immagine");

        if (super.addField("subtitle", subtitleField)) {
            leftContainerEl.appendChild(super.wrapField(subtitleField.build(), "Sottotitolo (opzionale)"))
        }

        if (super.addField("subtitleStyle", subtitleStyleField)) {
            leftContainerEl.appendChild(super.wrapField(subtitleStyleField.build(), "Stile sottotitolo"))
        }

        if (super.addField("text", textField)) {
            leftContainerEl.appendChild(super.wrapField(textField.build(), "Testo"))
        }

        if (super.addField("image", imageField)) {
            rightContainerEl.appendChild(super.wrapField(imageField.build(), "Immagine"))
        }

        if (super.addField("imageCaption", captionField)) {
            rightContainerEl.appendChild(super.wrapField(captionField.build(), "Didascalia (opzionale)"))
        }

        if (super.addField("imageCaptionPosition", captionPositionField)) {
            rightContainerEl.appendChild(super.wrapField(captionPositionField.build(), "Posizione didascalia"))
        }

        columnContainerEl.appendChild(leftContainerEl);
        columnContainerEl.appendChild(rightContainerEl);

        super.addToLayout(columnContainerEl);
    }

    init(el) {
        super.init(el);

        // TODO: encapsulate event binding in SelectField and propagate event from field
        for (var [lang, el] of super.getField("imagePosition").getComponents()) {

            el.addEventListener("change", (e) => {
                this.swapColumns(e.currentTarget);
            });

            if (el.value == "sx") {
                this.swapColumns(el);
            }
        }
    }

    swapColumns(imagePositionEl) {
        const blockEl = Utils.FindAncestorByClass(imagePositionEl, "block-item");
        const container = super.find(blockEl, ".columns-container");
        const leftContainerEl = super.find(container, ".left-column");
        const rightContainerEl = super.find(container, ".right-column");
        Utils.FadeSwapElements(leftContainerEl, rightContainerEl);
    }

    delete() {
        return super.getField("image").delete();
    }
}

export default ImageTextRender


