import Component from "~/base/Component";
import Utils from "~/base/Utils";
import RenderFactory from "~/components/render/RenderFactory";
import dragula from "dragula";
import _ from "lodash";
import classie from "classie";
import Cookies from "cookies-js";
import base64 from 'base-64';

class PageComposer extends Component {

    constructor(opts) {
        super(null, opts);

        this.blocks = Array();

        this.blockAdded = this.blockAdded.bind(this);
        this.getContent = this.getContent.bind(this);
        this.onDelete = this.onDelete.bind(this);
        this.save = this.save.bind(this);
        this.onChildChanged = this.onChildChanged.bind(this);
    }

    getDefaultOptions() {
        return {
            sources: [
                //"cookie:pagecomposer",
                "element:input[name=aq_pagecomposer_value]"
            ],
            targets: [
                //"cookie:pagecomposer",
                "element:input[name=aq_pagecomposer_value]"
            ],
            labels: {
                delete: "Delete",
                deleteTitle: "Confirm delete",
                deleteMessage: "Do you want to delete the block ?",
                confirm: "Confirm",
                cancel: "Cancel"
            },
            languageSelector: "#cmbLanguages",
            endpoints: {
                getPreview: 'getPreview',
                upload: 'upload',
                delete: 'delete',
            }
        }
    }

    init() {
        this.wrapper = document.getElementById('aq-composer-wrapper');
        if (!this.wrapper)
            return;

        this.composerContent = this.wrapper.querySelectorAll(".composer-content")[0];
        this.composerToolbar = this.wrapper.querySelectorAll(".composer-toolbar")[0];

        this.load();

        this.saveBtn = document.getElementById('saveBtn');
        if (this.saveBtn) {
            this.saveBtn.addEventListener("click", this.save);
        }

        this.renderExisting();

        this.bindDeleteBlock();

        const languageSelectorEl = this.getLanguageSelectorElement();
        if (languageSelectorEl) {
            languageSelectorEl.addEventListener("change", () => {
                this.setCurrentLanguage(languageSelectorEl.value);
            });
        }

        dragula([this.composerToolbar, this.composerContent], {
            copySortSource: false,
            copy: (el, source) => {
                return source === this.composerToolbar;
            },
            accepts: (el, target) => {
                return target === this.composerContent;
            },
            moves: (el, container, handle) => {
                return classie.has(handle, "task-move");
            }
        }).on("drop", this.blockAdded);
    }

    bindDeleteBlock() {
        this.composerContent.querySelectorAll(".task-delete").forEach((el) => {
            
            if (Utils.Data(el, "aq_pagecomposerclickbinded") === "1")
                return;

            if (!el.parentNode.classList.contains("block-task"))
                return;

            el.addEventListener("click", (e) => {
                this.onDelete(e.currentTarget);
            });

            Utils.Data(el, "aq_pagecomposerclickbinded", "1");
        });
    }

    onDelete(el) {
        const blockEl = Utils.FindAncestorByClass(el, "block-item");
        const uid = Utils.Data(blockEl, "pagecomposeruid");

        Utils.AlertWarning(this.settings.labels.deleteTitle, this.settings.labels.deleteMessage, true)
            .then(() => {
                if (uid && this.blocks[uid]) {
                    const block = this.blocks[uid];
                    block.delete()
                        .then(() => {
                            delete this.blocks[uid];
                            blockEl.parentNode.removeChild(blockEl);
                            this.onChildChanged();
                        })
                        .catch(() => {
                            Utils.AlertError();
                        });
                }
            });
    }

    renderExisting() {
        this.composerContent.querySelectorAll(".block-item").forEach(function (block) {
            this.renderBlock(block);
        }, this);
    }

    getSourceValue(source) {
        const sourcePath = _.split(source, ":");
        if (sourcePath[0] && sourcePath[1]) {
            switch (sourcePath[0]) {
                case "cookie":
                    return Cookies.get(sourcePath[1]);
                case "element":
                    const valueEl = document.querySelector(sourcePath[1]);
                    if (valueEl)
                        return valueEl.value;
            }
        }
    }

    setTargetValue(target, value) {
        const targetPath = _.split(target, ":");
        if (targetPath[0] && targetPath[1]) {
            switch (targetPath[0]) {
                case "cookie":
                    Cookies.set(targetPath[1], value);
                case "element":
                    const valueEl = document.querySelector(targetPath[1]);
                    if (valueEl)
                        valueEl.value = value;
            }
        }
    }

    load() {

        var loaded = false;
        this.settings.sources.forEach(function (source) {
            if (loaded)
                return;
            
            const savedContent = this.getSourceValue(source);
            if (savedContent) {
                const savedData = Utils.Base64ToObj(savedContent);
                loaded = this.loadData(savedData);
            }
        }, this);

        return loaded;
    }

    loadData(data) {
        
        if (data.blocks && Array.isArray(data.blocks)) {
            data.blocks.forEach(function (block) {
                const blockEl = document.createElement("div");
                classie.add(blockEl, "block-item");
                Utils.Data(blockEl, "pagecomposerrender", block.type);
                Utils.Data(blockEl, "pagecomposerblockrendered", 0);
                Utils.Data(blockEl, "pagecomposeredit", 1);
                blockEl.style.display = "none";
                blockEl.innerText = Utils.ObjToBase64(block.data);
                this.composerContent.appendChild(blockEl);
            }, this);
            return true;
        }
        return false;
    }

    save() {
        console.log(JSON.stringify(this.getContent()));
        const state = Utils.ObjToBase64(this.getContent());
        this.settings.targets.forEach(function (target) {
            this.setTargetValue(target, state);
        }, this);
    }

    blockAdded(el, target, source, sibling) {
        if (target !== this.composerContent)
            return false;

        this.renderBlock(el);
        this.bindDeleteBlock();
        this.onChildChanged();
    }

    onChildChanged() {
        this.save();
    }

    renderBlock(el) {
        const renderClass = Utils.Data(el, "pagecomposerrender");
        const alreadyRendered = Utils.Data(el, "pagecomposerblockrendered");
        if (renderClass && renderClass.trim().length > 0 && alreadyRendered != 1) {
            this.removePlaceHolder();
            const render = RenderFactory.create(renderClass, this.getSettingsForChild());
            render.init(el);
            this.blocks[render.id()] = render;

        }
    }

    getSettingsForChild() {
        return {
            changeCallback: this.onChildChanged,
            languages: this.getLanguages(),
            currentLang: this.getCurrentLanguage(),
            endpoints: this.settings.endpoints
        }
    }

    getLanguageSelectorElement() {
        return this.settings.languageSelector.substring(0, 1) === "#"
            ? document.getElementById(this.settings.languageSelector.substring(1))
            : super.find(document, this.settings.languageSelector);
    }

    getLanguages() {

        var result = {};

        Array.from(this.getLanguageSelectorElement().querySelectorAll("option")).forEach(function (element) {
            result[element.value] = element.innerText;
        }, this);

        return result;
    }

    getCurrentLanguage() {
        return this.getLanguageSelectorElement().value;
    }

    setCurrentLanguage(lang) {
        for (var key in this.blocks) {
            let block = this.blocks[key];

            if (block.setCurrentLanguage && typeof block.setCurrentLanguage === "function") {
                block.setCurrentLanguage(lang);
            }
        }
    }

    removePlaceHolder() {
        const placeHolderEl = this.composerContent.querySelectorAll(".composer-title");
        if (placeHolderEl && placeHolderEl.length > 0)
            placeHolderEl[0].parentNode.removeChild(placeHolderEl[0]);
    }

    getContent() {
        var result = Array();
        const blocks = this.composerContent.querySelectorAll(".block-item");
        blocks.forEach(function (block) {
            const uid = Utils.Data(block, "pagecomposeruid");
            if (uid && this.blocks[uid]) {
                result.push({
                    "type": this.blocks[uid].constructor.name.replace("Render", ""),
                    "data": this.blocks[uid].getValue()
                });
            }
        }, this);

        return {
            blocks: result
        };
    }
}

export default PageComposer


