import Component from "~/base/Component"
import Utils from "~/base/Utils"

class BaseField extends Component {

    constructor(opts) {
        super(null, opts);
        this.fieldName = "";
        this.components = new Map();
    }

    init() {

    }

    build() {
        if (this.settings.languages) {
            const localizedFieldContainer = document.createElement("div");
            localizedFieldContainer.classList.add("translation-container");
            for (var lang in this.settings.languages) {
                let localizedFieldEl = this.createLocalizedField(lang);
                if (localizedFieldEl) {
                    this.addComponent(lang, localizedFieldEl);
                    localizedFieldContainer.appendChild(localizedFieldEl);                    
                    if (lang !== this.settings.currentLang)
                        localizedFieldEl.style.display = 'none';
                }
            }
            return localizedFieldContainer;
        } else {
            let localizedFieldEl = this.createLocalizedField(Utils.NoLang());
            if (localizedFieldEl) {
                this.addComponent(Utils.NoLang(), localizedFieldEl);
                return localizedFieldEl;
            }
            return null;
        }
    }

    createLocalizedField (lang) {
        throw "Unimplemented 'createLocalizedField' in " + this.constructor.name;
    }

    getDefaultOptions() {
        return {
            changeCallback: null,
            languages: null,
            currentLang: null
        }
    }

    getComponents () {
        return this.components;
    }

    setCurrentLang (lang) {

    }

    addComponent(lang, el) {
        if (el && lang) {
            this.components.set(lang, el);
            el.addEventListener("change", () => {
                this.signalChange();
            });
        }        
    }

    signalChange () {
        if (this.settings.changeCallback && typeof this.settings.changeCallback === 'function')
            this.settings.changeCallback();
    }

    getValueFromLocalizedField (el, lang) {
        return (el ? el.value : null);
    }

    setValueToLocalizedField (el, value, lang) {
        if (el) 
            el.value = value;
    }

    getValue() {
        if (this.components.size <= 0)
            return null;

        if (this.components.size === 1 && this.components.keys().next().value === Utils.NoLang())
            return this.getValueFromLocalizedField(this.components.get(Utils.NoLang()), Utils.NoLang());

        var result = {};
        for (var [lang, el] of this.components)
            result[lang] = this.getValueFromLocalizedField(el, lang);
        return result;
    }

    setValue(value) {
        if (this.components.size <= 0)
            return null;

        if (this.components.size === 1 && this.components.keys().next().value === Utils.NoLang())
            return this.setValueToLocalizedField(this.components.get(Utils.NoLang()), value, Utils.NoLang());

        for (var [lang, el] of this.components) {
            if (value[lang])
                this.setValueToLocalizedField(el, value[lang], lang);
        }
    }

    getFieldName() {
        return this.fieldName;
    }

    setFieldName(value) {
        this.fieldName = value;
    }

    setCurrentLanguage (currentLanguage) {
        for (var [lang, el] of this.getComponents()) {
            if (Utils.NoLang() === lang || lang === currentLanguage)
                el.style.display = "block";
            else
                el.style.display = "none";
        }
    }
}

export default BaseField


