import BaseRender from "~/components/render/BaseRender"
import * as Fields from "~/components/fields"
import _ from 'lodash';

// HeaderBlockItem
class HeaderRender extends BaseRender {

    constructor(opts) {
        super(opts);
    }

    createFields() {
        /* Image field */
        const imageField = new Fields.ImageField(super.getSettingsForChild(false))

        /* Title field */
        const titleField = new Fields.RichTextField(super.getSettingsForChild(true));

        /* Abstract field */
        const abstractField = new Fields.RichTextField(super.getSettingsForChild(true, {
            showCode: true
        }));

        /* Title style field */
        const titleStyleField = new Fields.SelectField(super.getSettingsForChild(false, {
            "values": {
                "normal": "Normale",
                "gradient": "Gradiente arancione"
            }
        }));

        super.appendField("image", imageField, "Immagine");
        super.appendField("title", titleField, "Title");
        super.appendField("titleStyle", titleStyleField, "Title style");
        super.appendField("abstract", abstractField, "Abstract");
    }

    delete() {
        return super.getField("image").delete();
    }
}

export default HeaderRender


