import Emitter from './Emitter';

import throttle from 'lodash/throttle';

class Config {

    constructor() {

        this.mq = this.oldMq = '';

        this.$html = document.getElementsByTagName('html')[0];

        this.$document = document;

        this.$body = document.body;

        this.lang = this.$html.getAttribute('lang') || 'it';

        this.windoInfo = {
            width: 1,
            height: 1
        };

        this.events = {
            PAGE_INIT: 'Emitter:init',
            WINDOW_RESIZE: 'window:resize',
            WINDOW_GET_INFO: 'window:hasGetInfo',
            RESIZE: 'resize',
            MQ_CHANGED: 'mq:changed',
            SCREEN_UPDATED: 'screen:updated',
            CLICK: 'click',
            IMAGES_LOADED: 'loader:images-loaded',
            LOADER_GONE: 'loader:gone',
            KEYUP: 'keyup',
            MOUSEOVER: 'mouseover',
            MOUSEENTER: 'mouseenter',
            MOUSELEAVE: 'mouseleave',
            SCROLL: 'scroll:adjusted',
            SCROLL_NATIVE: 'scroll',
            MOUSEWHEEL: (/Firefox/i.test(navigator.userAgent)) ? 'DOMMouseScroll' : 'mousewheel',
            CANPLAYTHROUGH: 'canplaythrough',
            NOTES_LOADED: 'notes::loaded'
        };

        this.classes = {
            ACTIVE: 'is-active',
            INACTVIE: 'is-inactive',
            IS_SHOWING_MODAL: 'is-modal-active',
            NO_SCROLL: 'no-scroll'
        };

        this.STYLE_URL_REGEXP = /url\(("|'|)([\/:\w-\.]+(?:\.png|\.jpg|\.webp|\.gif))\1\)/g;

        this.handleWindowInfo();

        this.getWindowInfo();

        this.updateCurrentMq();

        this.getMqInfo();

    }

    handleWindowInfo() {

        this.getWindowInfo(false);

        Emitter.on(this.events.WINDOW_RESIZE, () => {
            this.getWindowInfo();
        });

        window.addEventListener(this.events.RESIZE, throttle(() => {

            if (!Modernizr.touchevents) {
                Emitter.emit(this.events.WINDOW_RESIZE);
            }

        }, 100));

    }

    getWindowInfo(emit = true) {

        this.windoInfo.width = window.innerWidth
            || document.documentElement.clientWidth
            || document.body.clientWidth;

        this.windoInfo.height = window.innerHeight
            || document.documentElement.clientHeight
            || document.body.clientHeight;

        if (emit) {
            Emitter.emit(this.events.WINDOW_GET_INFO);
        }

    }

    updateCurrentMq() {

        this.oldMq = this.mq;
        this.mq = window.getComputedStyle(document.body, '::after').content.replace(/['"]+/g, '');

        if (this.mq !== this.oldMq) {
            Emitter.emit(this.events.MQ_CHANGED);
        }
    }

    getMqInfo() {

        let content = window.getComputedStyle(document.body, ':before').content,
            result;

        const parseRegExp = /\{?\\?"([a-z0-9\-]+)\\?":([0-9]+)/g;

        const _mqMin = (mq) => {
            const keys = [];
            if (!this._mqMatrix) {
                return keys;
            }
            for (let key of this._mqMatrix.keys()) {
                keys.push(key);
            }
            return keys.indexOf(mq) !== -1 ? keys.slice(0, keys.indexOf(mq) + 1) : [];
        };

        this._mqMatrix = new Map();

        while ((result = parseRegExp.exec(content)) !== null) {
            this._mqMatrix.set(result[1], parseInt(result[2], 10));
        }

        this.mqTo = (mq) => {
            return _mqMin(this.mq).indexOf(mq) !== -1;
        };

        this.mqUntil = (mq) => {
            return _mqMin(this.mq).indexOf(mq) === -1;
        };

        Emitter.on(this.events.WINDOW_RESIZE, () => {
            this.updateCurrentMq();
        });

    }

}

export default new Config();


