import guid from 'guid';
import classie from 'classie';
import Utils from '~/base/Utils';
import merge from 'lodash/merge';

class Component {

    constructor(element, options = {}) {
        this.element = typeof element === 'string' ? { container: document.querySelector(element) } : { container: element };

        this.settings = merge({}, this.getDefaultOptions(), options);
        this.events = {};
        this.state = new Map();
        this.listeners = new Map();

        this._id = guid.create().toString().replace(/\-/ig, "0");
        this.id = this.id.bind(this);
    }

    id() {
        return this._id;
    }

    query(selector) {
        return Array.from(this.element.container.querySelectorAll(selector));
    }

    queryClass(selector) {
        return Array.from(this.element.container.getElementsByClassName(selector));
    }

    getDefaultOptions() {
        return {};
    }

    setInitialState() {
        return this.state;
    }

    getState(key = undefined) {
        return (typeof key === 'undefined') ? this.state : this.state.get(key);
    }

    setState(key, newValue) {
        const oldValue = this.state.get(key);
        if (oldValue !== newValue) {
            this.state.set(key, newValue);
        }
    }

    getListeners() {
        return this.listeners;
    }

    setListener(key, context, value) {

        const scope = (typeof context === 'string') ? document.querySelectorAll(context) : context;
        const addListener = (key, scope, value) => {
            if (scope.hasOwnProperty(length)) {
                for (let i = scope.length; i--;) {
                    scope[i].addEventListener(key, value, false);
                }
            } else {
                scope.addEventListener(key, value, false);
            }
        };

        if (this.listeners.has(scope)) {
            let oldMap = this.listeners.get(scope);
            if (oldMap.has(key)) {
                let oldFunctions = oldMap.get(key);
                if (oldFunctions.indexOf(value) === -1) {
                    oldFunctions.push(value);
                    addListener(key, scope, value);
                }
            } else {
                const newItem = new Map();
                newItem.set(key, [value]);
                this.listeners.set(scope, newItem);
                addListener(key, scope, value);
            }
        } else {
            const newItem = new Map();
            newItem.set(key, [value]);
            this.listeners.set(scope, newItem);
            addListener(key, scope, value);
        }

        return this.listeners;
    }

    data(key, value) {
        if (value !== undefined) {
            Utils.Data(key, value);
        } else {
            return Utils.Data(key);
        }
    }

    getSizeForQueryString(el, defaultHeight) {
        if (el.offsetHeight == 0)
            return "height=" + defaultHeight;
        else
            return "height=" + el.offsetHeight + "&width=" + el.offsetWidth;
    }

    find(el, selector) {
        var nodes = el.querySelectorAll(selector);
        return nodes.length <= 0 ? null : nodes[0];
    }
}

export default Component;


