import BaseField from "~/components/fields/BaseField"
import Utils from "~/base/Utils"

import tinymce from 'tinymce/tinymce'
import 'tinymce/themes/modern/theme'
import 'tinymce/plugins/lists'
import 'tinymce/plugins/link'
import 'tinymce/plugins/code'

class RichTextField extends BaseField {

    constructor(opts) {
        super(opts);
    }

    getDefaultOptions() {
        return {
            "toolbar": 'undo redo | bold italic underline | alignleft aligncenter alignright | bullist numlist | removeformat | link unlink',
            "showCode": false,
            "labels": {
                "placeHolder": "Enter text here ..."
            }
        }
    }

    createLocalizedField (lang) {

        const uid = this.id() + "-" + lang;
        const fieldEl = document.createElement("div");

        fieldEl.classList.add("composer-field", "tinymce");
        fieldEl.setAttribute("id", "richtext-" + uid);
        fieldEl.setAttribute("data-lang", lang);
        if (Utils.NoLang() !== lang)
            fieldEl.setAttribute("title", this.settings.languages[lang]);

        return fieldEl;
    }

    init() {
        for (var [lang, el] of super.getComponents()) 
        {
            let uid = this.id() + "-" + lang;
            let toolbar = this.settings.toolbar;
            if (toolbar.indexOf("code") < 0)
                toolbar += (this.settings.showCode ? ' | code' : '')

            tinymce.init({
                inline: true,
                statusbar: false,
                menubar: false,
                selector: "#richtext-" + uid,
                skin: true,
                plugins: ['lists', 'link', 'code'],
                toolbar: toolbar,
                init_instance_callback: (editor) => {
                    editor.on('Change', () => {
                        super.signalChange();
                    });
                }
            });
        }
    }

    getValueFromLocalizedField(el, lang) {
        const uid = this.id() + "-" + lang;
        return tinymce.get("richtext-" + uid).getContent();
    }

    setValueToLocalizedField (el, value, lang) {
        const uid = this.id() + "-" + lang;
        return tinymce.get("richtext-" + uid).setContent(value);
    }
}

export default RichTextField


